import React, { Fragment, useState, useEffect } from "react";
import SwipeableDrawer from "@material-ui/core/SwipeableDrawer";
import axios from "axios";

export default function SwipeableTemporaryDrawer() {
  const [info, setInfo] = useState();
  const [labs, setLabs] = useState([]);
  const user = localStorage.getItem("user");
  const [state, setState] = useState({
    right: false,
  });

  useEffect(() => {
    axios
      .get("https://devoasys.1wa.org/portal/users/" + user)
      .then(({ data }) => {
        setInfo(data);

        const email = data.email;

        axios
          .get("https://devoasys.1wa.org/portal/organizations/" + email)
          .then(({ data }) => {
            setLabs(data);
          });
      });
  }, []);

  const toggleDrawer = (side, open) => (event) => {
    if (
      event &&
      event.type === "keydown" &&
      (event.key === "Tab" || event.key === "Shift")
    ) {
      return;
    }

    setState({ ...state, [side]: open });
  };

  return info ? (
    <Fragment>

      <div className="contain-filter fade-in">
        <div className="filter-sticky">
          <div className="filter-left">
            <small>Participant</small>
            <h2>
              {info.orgName}
              <br />
              <span>{info.orgCode}</span>
            </h2>
          </div>

          <div className="filter-right">
            <div className="task-switch">
              You are logged in as{" "}
              <strong>
                {info.firstName} {info.lastName}
              </strong>{" "}
              <br />
              <a className="link-org" onClick={toggleDrawer("right", true)}>
                VIEW ORGANIZATION
              </a>
            </div>
          </div>
        </div>
      </div>

      <SwipeableDrawer
        anchor="right"
        open={state.right}
        onClose={toggleDrawer("right", false)}
        onOpen={toggleDrawer("right", true)}
        id="user"
      >
        <div>
          <div className="dock-top">
            <a className="link-close" onClick={toggleDrawer("right", false)}>
              <i className="material-icons">close</i>
            </a>
          </div>
          <ul className="manage-labs">
            <li>
              <div>XYZ</div>
              <div style={{ textAlign: "right" }}>ROLE</div>
            </li>

            {labs.map((item, index) => (
              <li key={index}>
                <div>
                  <h3>{item.name}</h3>
                  {item.code}
                  <small>{item.typeName}</small>
                  <span className="icon-switch">Switch to this XYZ?</span>
                </div>
                <div style={{ textAlign: "right" }}>
                  <span className="status-role">{item.userRole.name}</span>
                </div>
              </li>
            ))}
          </ul>
          {/* <div className="dock-drawer-bottom">
            dock at bottom can contain whatever
          </div> */}
        </div>
      </SwipeableDrawer>
    </Fragment>
  ) : (
    <Fragment></Fragment>
  );
}
