import React, { Fragment } from "react";
import { OasysContext } from "./Context";
import SelectMulti from "./SelectMulti";
import { animateScroll as scroll } from "react-scroll";
import TextField from "@material-ui/core/TextField";

export default class ActivitiesFilters extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			keyword: "",
			disciplineFilter: "",
			selectDisciplines: [],
			showOpen: true,
			showUpcoming: true,
			showClosed: false,
			showActions: false,
			showRegistration: true,
			showEval: true,
			reloading: false,
			today: Date.now(),
		};
	}

	handleChange = () => {
		if (event && event.target) {
			const { value } = event.target;
			this.setState({ keyword: value });
			window.scrollTo(0, 0);
		}
	};

	handleSelectDiscipline = (event) => {
		this.setState({ disciplineFilter: event.target.value });
		window.scrollTo(0, 0);
	};

	handleClearFilters = () => {
		this.setState({
			disciplineFilter: "",
			showOpen: true,
			showUpcoming: true,
			showRegistration: true,
			showEval: true,
			showClosed: false,
			showActions: false,
			keyword: "",
		});
	};

	handleOpenTE = () => {
		if (this.state.showOpen == false) {
			this.setState({ showOpen: true, showActions: false });
		} else {
			this.setState({ showOpen: false });
		}
		this.goTop();
	};

	handleClosedTE = () => {
		if (this.state.showClosed == false) {
			this.setState({ showClosed: true, showActions: false });
		} else {
			this.setState({ showClosed: false });
		}
		this.goTop();
	};

	handleClearKeyword = () => {
		this.setState({ keyword: "" });
		this.goTop();
	};

	handleClearDiscipline = () => {
		this.setState({ disciplineFilter: "" });
		this.goTop();
	};

	handleUpcomingTE = () => {
		if (this.state.showUpcoming == false) {
			this.setState({ showUpcoming: true, showActions: false });
		} else {
			this.setState({ showUpcoming: false });
		}
		this.goTop();
	};

	handleRegistrationTE = () => {
		if (this.state.showRegistration == false) {
			this.setState({ showRegistration: true, showActions: false });
		} else {
			this.setState({ showRegistration: false });
		}
		this.goTop();
	};

	handleEvalTE = () => {
		if (this.state.showEval == false) {
			this.setState({ showEval: true, showActions: false });
		} else {
			this.setState({ showEval: false });
		}
		this.goTop();
	};

	handleActions = () => {
		if (this.state.showActions == false) {
			this.setState({
				showActions: true,
				showUpcoming: true,
				showRegistration: true,
				showOpen: true,
				showEval: true,
				showClosed: false,
				reloading: true,
			});

			setTimeout(
				function () {
					this.setState({ reloading: false });
				}.bind(this),
				1000
			);
		}
		// } else {
		// 	this.setState({ showActions: false, reloading: false });
		// }
		this.goTop();
	};

	goTop = () => {
		scroll.scrollToTop({
			duration: 400,
			delay: 0,
		});
	};

	static contextType = OasysContext;

	render() {
		const value = this.context;

		let {
			keyword,
			disciplineFilter,
			selectDisciplines,
			showOpen,
			showClosed,
			showUpcoming,
			showActions,
			showEval,
			actions = value.actions,
			reloading,
			showRegistration,
		} = this.state;

		return (
			<div className="contain">
				{value.loadingAPI ? (
					<div className="load"></div>
				) : (
					<div className="contain-filter fade-in">
						<div className="filter-sticky">
							<div id="filters">
								<h3>FILTER</h3>

								<div className="message-filter fade-in">
									{reloading ? (
										<div className="reload"></div>
									) : showActions ? (
										<Fragment>
											<a className="link-hide" onMouseDown={this.handleActions}>
												DISPLAY
											</a>{" "}
											ALL ACTIVITIES.
										</Fragment>
									) : (
										<Fragment>
											<a className="link-show" onMouseDown={this.handleActions}>
												DISPLAY
											</a>{" "}
											all <span>{actions}</span> activities that require my
											attention.
										</Fragment>
									)}
								</div>

								<div className="filter-rollups">
									<div className="toggle-group">
										<div className="toggle-group-label">
											<label>BY TEST EVENT</label>
										</div>

										<div className="toggle-group-row">
											<div
												className={
													showUpcoming ? "task-rollup-selected" : "task-rollup"
												}
												onClick={this.handleUpcomingTE}
											>
												{value.filters[0].TE_UPCOMING.data.number}{" "}
												{value.filters[0].TE_UPCOMING.tag}
											</div>

											<div
												className={
													showRegistration
														? "task-rollup-selected"
														: "task-rollup"
												}
												onClick={this.handleRegistrationTE}
											>
												{value.filters[0].TE_REGISTRATION.data.number}{" "}
												{value.filters[0].TE_REGISTRATION.tag}
											</div>

											<div
												className={
													showOpen ? "task-rollup-selected" : "task-rollup"
												}
												onClick={this.handleOpenTE}
											>
												{value.filters[0].TE_OPEN.data.number}{" "}
												{value.filters[0].TE_OPEN.name}
											</div>

											<div
												className={
													showEval ? "task-rollup-selected" : "task-rollup"
												}
												onClick={this.handleEvalTE}
											>
												{value.filters[0].TE_EVALUATION.data.number}{" "}
												{value.filters[0].TE_EVALUATION.tag}
											</div>
										</div>
									</div>

									{(showOpen || showUpcoming || showEval) && (
										<div className="filter-group">
											<SelectMulti
												handleSelectDiscipline={this.handleSelectDiscipline.bind(
													this
												)}
												disciplines={selectDisciplines}
											/>{" "}
											<TextField
												id="standard-basic"
												onChange={this.handleChange}
												autoComplete="off"
												spellCheck="false"
												label="BY KEYWORD"
												defaultValue={keyword}
												placeholder="Filter by Discipline, Program, Order Code, Subscription, or Analyte"
												style={{
													marginLeft: "24px",
													marginRight: "0",
													width: "425px",
												}}
											/>
										</div>
									)}
								</div>

								<div className="task-status-filter">
									{showOpen ||
									showUpcoming ||
									showClosed ||
									showActions ||
									showEval ? (
										<Fragment>
											Currently displaying Activities for
											{showOpen && (
												<span
													className="link-small"
													onClick={this.handleOpenTE}
												>
													Open Test Events <i className="fas fa-times"></i>
												</span>
											)}{" "}
											{showUpcoming && (
												<span
													className="link-small"
													onClick={this.handleUpcomingTE}
												>
													Upcoming Test Events <i className="fas fa-times"></i>
												</span>
											)}{" "}
											{showRegistration && (
												<span
													className="link-small"
													onClick={this.handleRegistrationTE}
												>
													Test Events in Registration{" "}
													<i className="fas fa-times"></i>
												</span>
											)}{" "}
											{showClosed && (
												<span
													className="link-small"
													onClick={this.handleClosedTE}
												>
													Closed Test Events <i className="fas fa-times"></i>
												</span>
											)}{" "}
											{showEval && (
												<span
													className="link-small"
													onClick={this.handleEvalTE}
												>
													Test Events in Evaluation
													<i className="fas fa-times"></i>
												</span>
											)}{" "}
											{!showActions ? "" : "and "}
											{showActions && (
												<Fragment>
													<span
														className="link-small"
														onClick={this.handleActions}
													>
														{actions} action items{" "}
														<i className="fas fa-times"></i>
													</span>
												</Fragment>
											)}
											{keyword && (
												<Fragment>
													{" "}
													<span
														className="link-small"
														onClick={this.handleClearKeyword}
													>
														{" "}
														{keyword}
														<i className="fas fa-times"></i>
													</span>
												</Fragment>
											)}
											{disciplineFilter && (
												<Fragment>
													{" "}
													<span
														className="link-small"
														onClick={this.handleClearDiscipline}
													>
														{" "}
														{disciplineFilter}
														<i className="fas fa-times"></i>
													</span>
												</Fragment>
											)}
										</Fragment>
									) : (
										<Fragment>
											Select a filter from above to display data for Test
											Events.
										</Fragment>
									)}
								</div>

								{/* END */}
							</div>

							<div className="task-title"></div>
						</div>
					</div>
				)}
			</div>
		);
	}
}
