import React, { Fragment } from "react";
import ReactDOM from "react-dom";
import { Route, Switch, HashRouter as Router } from "react-router-dom";
import { Offline, Online } from "react-detect-offline";

import NavSignout from "./NavSignout";
import Top from "./Top";
import NavLogin from "./NavLogin";
import NavBrandedPublic from "./NavBrandedPublic";
import Nav from "./Nav";
import NavActivities from "./NavActivities";
// import Activities from "./Activities";
import Login from "./Login";
import Account from "./Account";
import Authorize from "./Authorize";
import ActivitiesFilters from "./ActivitiesFilters";

import { MyContext } from "./Context";

window.$locale = "en";

const user = localStorage.getItem("user");

class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			loading: true,
			profile: null,
		};
	}

	componentDidMount() {
		window.scrollTo(0, 0);
		this.setState({ loading: false });
	}

	render() {
		return (
			<Fragment>
				<Offline>
					<div className="offline">
						<h1>PLEASE CHECK YOUR INTERNET CONNECTION.</h1>
					</div>
				</Offline>

				<Online>
					{this.state.loading ? (
						<div className="load"></div>
					) : (
						<Fragment>
							<Router>
								<Switch>
									<Route
										exact
										path="/"
										render={(props) => (
											<Fragment>
												<NavLogin />
												<NavBrandedPublic {...props} />
												<Login {...props} />
											</Fragment>
										)}
									/>

									<Route
										exact
										path="/activities"
										render={(props) => (
												<MyContext>
													<Top />
													<Nav {...props} />
													<ActivitiesFilters {...props} />
												</MyContext>
										)}
									/>

									<Route
										exact
										path="/login"
										render={(props) => (
											<Fragment>
												<NavLogin />
												<NavBrandedPublic {...props} />
												<Login {...props} />
											</Fragment>
										)}
									/>
									<Route
										exact
										path="/authorize"
										render={(props) => (
												<MyContext>
													<Top />
													<Nav {...props} />
													<Authorize {...props} />
												</MyContext>
										)}
									/>
									<Route
										exact
										path="/signout"
										render={(props) => (
											<Fragment>
												<NavSignout />
												<NavBrandedPublic {...props} />
												<Login />
											</Fragment>
										)}
									/>

									<Route
										exact
										path="/account"
										render={(props) => (
											<MyContext>
												<NavActivities />
												<Nav {...props} />
												<Account />
											</MyContext>
										)}
									/>
								</Switch>
							</Router>
						</Fragment>
					)}
				</Online>
			</Fragment>
		);
	}
}

ReactDOM.render(<App />, document.getElementById("root"));
