import React, { Component } from "react";

const OasysContext = React.createContext();
const OasysProvider = OasysContext.Provider; // used for class components
// const OasysConsumer = OasysContext.Consumer; // used for function components

class MyContext extends Component {
	constructor(props) {
		super(props);

		this.state = {
			environment: [],
			userData: [],
			resources: [],
			loadingUser: true,
			loadingEnv: true,
			loadingAPI: true,
		};
	}

	// ENV
	async componentDidMount() {
		try {
			const requestEnv = Date.now();
			const response = await fetch(`conf/env.json`);
			const data = await response.json();
			this.setState({ environment: [data], envDate: new Date(data.created) });

			try {
				const resources = await fetch(data.resources_url),
					responseEnv = Date.now();
				const resourceData = await resources.json();
				this.setState({
					resources: [resourceData],
					loadingEnv: false,
					totalEnvTime: responseEnv - requestEnv,
				});
			} catch (error) {
				console.log(error);
			}
		} catch (error) {
			console.log(error);
			this.setState({
				errorEnv: error
			});
		}

		let user = localStorage.getItem("user");

		let USER_URL =
			this.state.environment[0].host_url +
			this.state.resources[0].GET_USER.path;
		USER_URL = USER_URL.replace("{username}", user);

		try {
			const requestUser = Date.now();
			const userData = await fetch(USER_URL),
				responseUser = Date.now();
			const userDataJSON = await userData.json();
			this.setState({
				userData: [userDataJSON],
				loadingUser: false,
				totalUserTime: responseUser - requestUser,
			});
		} catch (error) {
			console.log(error);
			this.setState({
				errorUser: error
			});
		}

		let ORG_URL =
			this.state.environment[0].host_url +
			this.state.resources[0].GET_ACTIVITIES.path;
		ORG_URL = ORG_URL.replace("{organization_code}", "QC1040");

		try {
			const requestContext = Date.now();
			const activitiesData = await fetch(ORG_URL),
				responseContext = Date.now();
			const activitiesDataJSON = await activitiesData.json();

			this.setState({
				activities: activitiesDataJSON.timelines,
				loadingAPI: false,
				disciplines: [activitiesDataJSON.disciplines],
				actions: activitiesDataJSON.filters.ACTION_REQUIRED.data.number,
				filters: [activitiesDataJSON.filters],
				totalContextTime: responseContext - requestContext,
			});
		} catch (error) {
			console.log(error);
			this.setState({
				errorAPI: error
			});
		}
	}

	render() {
		return (
			<OasysProvider value={{ ...this.state }}>
				{this.props.children}
			</OasysProvider>
		);
	}
}

export { OasysContext, MyContext };
