import React, { Fragment } from "react";
import { Link, withRouter } from "react-router-dom";
import TextField from "@material-ui/core/TextField";
import ReactTooltip from "react-tooltip";
import Moment from "react-moment";

const random = Math.floor(Math.random() * 1000);
const environmentPath = "conf/env.json?" + random;

export default class Login extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			loginActive: false,
			environment: [],
			loading: true,
			showMode: false,
		};
	}
	

	async componentDidMount() {
		try {
			const response = await fetch(environmentPath);
			const json = await response.json();
			this.setState({ environment: json, loading: false, formattedDate: new Date(json.created)   });
			// date conversion is to avoid deprecated moment ISO formatting provided from API, suggest standard ISO date format in API data
		} catch (error) {
			console.log(error);
		}
	}

	handleFormInput = (event) => {
		const { value } = event.target;
		window.$user = value;

		localStorage.setItem("user", value);

		this.setState({ loginActive: true });
	};

	enableMode = () => {
		this.setState({ showMode: true });
	};

	disableMode = () => {
		this.setState({ showMode: false });
	};

	submitForm = (event) => {
		if (event.charCode == 13 || event.keyCode == 13) {
			this.props.history.push({ pathname: "/authorize" });
		}
	};
	clickSubmitForm = (event) => {
		this.props.history.push({ pathname: "/authorize" });
	};

	render() {
		let { loginActive, environment, showMode, formattedDate } = this.state;

		return (
			<Fragment>
				<form noValidate autoComplete="off">
					<div className="login-container">
						<div className="login-item fade-in">
							<h3>OASYS</h3>
							<TextField
								id="standard-required"
								label="username"
								defaultValue=""
								margin="normal"
								autoComplete="off"
								autoFocus
								className="login-field"
								onKeyPress={this.submitForm}
								onChange={this.handleFormInput}
							/>

							<TextField
								id="standard-password-input"
								label="password"
								type="password"
								defaultValue=""
								autoComplete="current-password"
								margin="normal"
								className="login-field"
							/>

							<div className="nudge-lg"></div>

							{loginActive ? (
								<a className="link-sso" onClick={this.clickSubmitForm}>
									LOGIN
								</a>
							) : (
								// <Link to="/activities" className="link-sso">
								//   LOGIN
								// </Link>
								<a className="link-sso-disabled" data-tip data-for="login">
									LOGIN
								</a>
							)}

							<div className="nudge-md"></div>

							<Link to="/" className="link-forgot" data-tip data-for="forgot">
								forgot password?
							</Link>

							<ReactTooltip place="top" type="dark" effect="float" id="forgot">
								Coming Soon
							</ReactTooltip>

							<ReactTooltip place="top" type="dark" effect="float" id="login">
								Enter USERNAME and PASSWORD
							</ReactTooltip>

							{environment.env !== "prod" && (
								<Fragment>
									<div className="mode">

											{!showMode && (
												<a onClick={this.enableMode} className="mode-status">
													{/* <i className="fas fa-sitemap"></i> */}
													<i className="fas fa-power-off"></i>
												</a>
											)}
										<div className="mode-left">
										
											{showMode && (
												<Fragment>
													<a onClick={this.disableMode} className="mode-status">
														<i className="fas fa-times"></i>
													</a>
													<a
														className={
															environment.env == "test"
																? "mode-status--active"
																: "mode-status"
														}
														href="https://test-oasys.1wa.org"
													>
														TEST
													</a>
													<a
														className={
															environment.env == "dev"
																? "mode-status--active"
																: "mode-status"
														}
														href="https://dev-oasys.1wa.org"
													>
														DEV
													</a>
													<a
														className={
															environment.env == "freeze"
																? "mode-status--active"
																: "mode-status"
														}
														href="https://freeze-oasys.1wa.org"
													>
														FREEZE
													</a>
													<a
														className={
															environment.env == "demo"
																? "mode-status--active"
																: "mode-status"
														}
														href="https://demo-oasys.1wa.org"
													>
														DEMO
													</a>
													<a
														className={
															environment.env == "prod"
																? "mode-status--active"
																: "mode-status"
														}
														href="https://oasys.1wa.org"
													>
														PROD
													</a>
												</Fragment>
											)}
										</div>

										<div className="mode-right">
											{environment.host_url} <b>|</b>{" "}
											{`v` + environment.version} <b>|</b> {environment.server}{" "}
											<b>|</b>{" "}
											<Moment format="lll" date={formattedDate} />
										</div>
									</div>
								</Fragment>
							)}
						</div>
					</div>
				</form>
			</Fragment>
		);
	}
}
