
import React from 'react';
import TextField from '@material-ui/core/TextField';
import Autocomplete from '@material-ui/lab/Autocomplete';

export default function Playground(props) {

    const defaultProps = {
        options: Object.values(props.disciplines),
        getOptionLabel: (option) => option.name,
    };
  
    return (
    <div style={{ width: 200, marginLeft: "12px" }}>
      <Autocomplete
        {...defaultProps}
        id="Filter"
        onSelect={props.handleSelectDiscipline}
        renderInput={(params) => <TextField {...params} label="By Discipline" margin="normal"/>}
      />
    </div>
    );
}

