import React, { Fragment } from "react";
import { OasysContext } from "./Context";
import { Link } from "react-router-dom";
import ReactJson from "react-json-view";
import ReactTooltip from "react-tooltip";

export default class Authorize extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			envString: undefined,
			userString: undefined,
			contextString: undefined,
		};
	}

	componentDidMount() {}

	goActivities = () => {
		this.props.history.push({ pathname: "/activities" });
	};

	static contextType = OasysContext;

	render() {
		const value = this.context;

		if (value.environment.length > 0 && !value.environment[0].debug) {
			this.goActivities();
		}

		let {
			envString = value.environment[0],
			userString = value.userData[0],
			contextString = value,
			errorAPI = value.errorAPI,
			errorUser = value.errorUser,
			errorEnv = value.errorEnv,
		} = this.state;

		return (
			<div className="contain">
				<div className="nudge-lg"></div>
				<div className="context-metrics">
					<div className="box">
						<div className="control-item">
							<div className="control-icon">
								{!value.loadingEnv ? (
									<i className="fas fa-server pop-in"></i>
								) : errorEnv ? (
									<i className="fas fa-skull-crossbones error pop-in"></i>
								) : (
									<div className="fetching"></div>
								)}
							</div>
							<div>
								<h3>
									{!value.loadingEnv ? value.environment[0].env : "Environment"}
								</h3>{" "}
								{!value.loadingEnv ? (
									<Fragment>
										<sup>
											<i
												className="fas fa-check-circle success fade-in-delay"
												data-tip
												data-for="success-env"
											></i>
										</sup>
										<ReactTooltip
											place="top"
											type="dark"
											effect="float"
											id="success-env"
										>
											Data Ready in Context
										</ReactTooltip>
										<small>
											Server Response{" "}
											<strong>
												{(value.totalEnvTime / 1000).toFixed(1)} seconds
											</strong>
										</small>
									</Fragment>
								) : (
									errorEnv && (
										<div className="error">
											Network Error. Is your VPN connected?
										</div>
									)
								)}
							</div>
						</div>
						{!value.loadingEnv && (
							<div className="control-list fade-in">
								<div>
									<ReactJson
										name="environment"
										theme="grayscale:inverted"
										displayDataTypes={false}
										displayObjectSize={false}
										collapsed={true}
										enableClipboard={false}
										src={envString}
									/>
								</div>
							</div>
						)}
					</div>
					<div className="box">
						<div className="control-item">
							<div className="control-icon">
								{!value.loadingUser ? (
									<i className="fas fa-user pop-in"></i>
								) : errorUser ? (
									<i className="fas fa-skull-crossbones error pop-in"></i>
								) : (
									<div className="fetching"></div>
								)}
							</div>
							<div>
								<h3>User Data</h3>{" "}
								{!value.loadingUser ? (
									<Fragment>
										<sup>
											<i
												className="fas fa-check-circle success fade-in-delay"
												data-tip
												data-for="success-user"
											></i>
										</sup>
										<ReactTooltip
											place="top"
											type="dark"
											effect="float"
											id="success-user"
										>
											Data Ready in Context
										</ReactTooltip>
										<small>
											Server Response{" "}
											<strong>
												{(value.totalUserTime / 1000).toFixed(1)} seconds
											</strong>
										</small>
									</Fragment>
								) : (
									errorUser && (
										<div className="error">
											Network Error. Is your VPN connected?
										</div>
									)
								)}
							</div>
						</div>
						{!value.loadingUser && (
							<div className="control-list fade-in">
								<div>
									<ReactJson
										name="userData"
										theme="grayscale:inverted"
										displayDataTypes={false}
										displayObjectSize={false}
										enableClipboard={false}
										collapsed={true}
										src={userString}
									/>
								</div>
							</div>
						)}
					</div>

					<div className="box">
						<div className="control-item">
							<div className="control-icon">
								{!value.loadingAPI ? (
									<i
										className="fab fa-react pop-in"
										style={{ fontSize: "20px" }}
									></i>
								) : errorAPI ? (
									<i className="fas fa-skull-crossbones error pop-in"></i>
								) : (
									<div className="fetching"></div>
								)}
							</div>
							<div>
								<h3>Context</h3>{" "}
								{!value.loadingAPI ? (
									<Fragment>
										<sup>
											<i
												className="fas fa-check-circle success fade-in-delay"
												data-tip
												data-for="success-api"
											></i>
										</sup>
										<ReactTooltip
											place="top"
											type="dark"
											effect="float"
											id="success-api"
										>
											Data Ready in Context
										</ReactTooltip>
										<small>
											Server Response{" "}
											<strong>
												{(value.totalContextTime / 1000).toFixed(1)} seconds
											</strong>
										</small>
									</Fragment>
								) : (
									errorAPI && (
										<div className="error">
											Network Error. Is your VPN connected?
										</div>
									)
								)}
							</div>
						</div>

						{!value.loadingAPI && (
							<Fragment>
								<div className="control-list fade-in">
									<div>
										<ReactJson
											name="value"
											theme="grayscale:inverted"
											displayDataTypes={false}
											displayObjectSize={false}
											enableClipboard={false}
											collapsed={true}
											src={contextString}
										/>
									</div>
								</div>

								{!value.loadingAPI && (
									<Fragment>
										<div className="nudge-md"></div>
										<p>
											<strong>Context Enabled Routes</strong>
										</p>{" "}
										<div
											className="control-item"
											style={{
												padding: "6px",
												border: "1px dashed #D3D8DD",
												background: "rgba(255,255,255,.3)",
											}}
										>
											<div className="control-icon">
												<i className="fas fa-link pop-in"></i>
											</div>
											<div>
												<Link to="/activities" className="link-context">
													Activities
												</Link>{" "}
												<Link to="/account" className="link-context">
													Account
												</Link>
											</div>
										</div>
										<div className="nudge-md"></div>
										<p>
											<strong>Context Enabled Components</strong>
										</p>
										<div
											className="control-item"
											style={{
												padding: "6px",
												border: "1px dashed #D3D8DD",
												background: "rgba(255,255,255,.3)",
											}}
										>
											<div className="control-icon">
												<i className="fas fa-puzzle-piece pop-in"></i>
											</div>
											<div>
												<span>Authorize</span>
												<span>Activities</span>
												<span>Account</span>
												<span>UserSettings</span>
												<span>Nav</span>
												<span>Top</span>
											</div>
										</div>
									</Fragment>
								)}
							</Fragment>
						)}
					</div>

					{/* <div className="box">
						<h3>
							Activities API{" "}
							{!value.loadingAPI && (
								<i className="fab fa-react success fade-in-delay"></i>
							)}
						</h3>
						{value.loadingAPI ? (
							<div className="fetching"></div>
						) : (
							<Fragment>
								<div className="control-list fade-in">
									<small>Activities</small>
									<b>{value.activities.length}</b>

									<div className="nudge-sm"></div>

									<small>Action Items</small>
									<b>
										{value.actions}{" "}
										<Link to="/activities">View Activities</Link>
									</b>

									<div className="nudge-sm"></div>

									<small>Server Response</small>
									<div>
										{(value.totalContextTime / 1000).toFixed(1)} seconds
									</div>
								</div>
							</Fragment>
						)}
					</div> */}
				</div>
			</div>
		);
	}
}
