import React, { Fragment, useState, useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";
import SwipeableDrawer from "@material-ui/core/SwipeableDrawer";
import TextField from "@material-ui/core/TextField";
import { Link } from "react-router-dom";
import axios from "axios";

import ReactTooltip from "react-tooltip";

const useStyles = makeStyles({
	list: {
		width: 320,
	},
	fullList: {
		width: "auto",
	},
	formControl: {
		minWidth: 320,
		fontSize: "10px",
	},
	selectEmpty: {},
	textField: {
		minWidth: 320,
	},
});

export default function SwipeableTemporaryDrawer() {
	const classes = useStyles();
	const [info, setInfo] = useState([]);
	const user = localStorage.getItem("user");
	const [state, setState] = React.useState({
		right: false,
	});

	useEffect(() => {
		axios
			.get("https://devoasys.1wa.org/portal/users/" + user)
			.then(({ data }) => {
				setInfo(data);
			});
	}, []);

	const toggleDrawer = (side, open) => (event) => {
		if (
			event &&
			event.type === "keydown" &&
			(event.key === "Tab" || event.key === "Shift")
		) {
			return;
		}

		setState({ ...state, [side]: open });
	};

	const handleSignout = () => {
		localStorage.removeItem("user");
	};

	return (
		<Fragment>
			{state.right ? (
				<div className="layout-row flex-right">
					<a className="link-profile" onClick={toggleDrawer("right", false)}>
						{" "}
						<i className="fas fa-user-circle"></i>
					</a>{" "}
					{info.firstName} {info.lastName}{" "}
				</div>
			) : (
				<div className="layout-row flex-right" style={{ fontSize: "13px" }}>
					<Link to="/account" className="link-lab">
						{info.orgName} (<strong>{info.orgCode}</strong>)
					</Link>{" "}
					<a className="link-user" onClick={toggleDrawer("right", true)}>
						{info.firstName} {info.lastName}
					</a>
				</div>
			)}

			<SwipeableDrawer
				anchor="right"
				open={state.right}
				onClose={toggleDrawer("right", false)}
				onOpen={toggleDrawer("right", true)}
				id="user"
			>
				<div>
					<div className="dock-top">
						<a className="link-close" onClick={toggleDrawer("right", false)}>
							<i className="material-icons">close</i>
						</a>
					</div>
					<div className="nudge-xl"></div>
					<TextField
						id="title"
						label="Title"
						defaultValue=" "
						className={classes.textField}
						margin="normal"
						autoComplete="nope"
					/>
					<TextField
						id="firstname"
						label="First Name"
						defaultValue={info.firstName}
						className={classes.textField}
						margin="normal"
						autoComplete="nope"
					/>
					<TextField
						id="lastname"
						label="Last Name"
						defaultValue={info.lastName}
						className={classes.textField}
						margin="normal"
						autoComplete="nope"
					/>
					<TextField
						id="phone"
						label="Phone Number"
						defaultValue={info.phone}
						className={classes.textField}
						margin="normal"
						autoComplete="nope"
					/>
					<TextField
						id="email"
						label="Email"
						defaultValue={info.email}
						className={classes.textField}
						margin="normal"
						autoComplete="nope"
					/>
					<Link to="/login" className="link-text" data-tip data-for="username">
						Change Password?
					</Link>

					<ReactTooltip place="top" type="dark" effect="float" id="username">
						This will trigger SSO/Keycloak Flow
					</ReactTooltip>
					<div className="nudge-md"></div>
					<div className="dock-drawer">
						<a className="link-save" onClick={toggleDrawer("right", false)}>
							Save
						</a>{" "}
						<a className="link-cancel" onClick={toggleDrawer("right", false)}>
							Cancel
						</a>
					</div>

					<div className="nudge-lg"></div>

					<Link to="/login" className="link-signout" onClick={handleSignout}>
						Sign Out <i className="fas fa-sign-out-alt"></i>
					</Link>

					{/* <div className="dock-drawer-bottom">
            Something
          </div> */}
				</div>
			</SwipeableDrawer>
		</Fragment>
	);
}
