import React, { Fragment } from "react";
import ReactTooltip from "react-tooltip";

const setDate = new Date();
setDate.setFullYear(setDate.getFullYear() + 10, setDate.getMonth());

export default class Language extends React.Component {
  constructor(props) {
    super(props);

    const code = (function () {
      return {
        getCookie: function (name) {
          var value = "; " + document.cookie;
          var parts = value.split("; " + name + "=");
          if (parts.length == 2) {
            return parts.pop().split(";").shift();
          } else {
            document.cookie =
              "change.language.locale.cookie=en:CA; expires=" +
              setDate +
              "; path=/";
            document.cookie =
              "change.languageshort.locale.cookie=en; expires=" +
              setDate +
              "; path=/";
          }
        },
      };
    })();

    this.state = {
      programs: [],
      loading: true,
      copied: false,
      reminder: null,
      sent: false,
      keyword: "",
      publishing: false,
      currentLanguage:
        code.getCookie("change.language.locale.cookie") || "en:CA",
      shortLanguage:
        code.getCookie("change.languageshort.locale.cookie") || "en",
    };

    window.$locale = this.state.shortLanguage; //global localization variable
  }

  handleClick(language, languageshort) {
    document.cookie =
      "change.language.locale.cookie=" +
      language +
      "; expires=" +
      setDate +
      "; path=/";
    document.cookie =
      "change.languageshort.locale.cookie=" +
      languageshort +
      "; expires=" +
      setDate +
      "; path=/";
    this.setState({ currentLanguage: language, shortLanguage: languageshort });

    window.location.reload();
  }

  render() {
    const { shortLanguage } = this.state;

    return (
      <Fragment>
        <small className="lang">
          <label>Language</label>
          <ul>
            <li>
              <a
                data-tip
                data-for="english"
                className={shortLanguage == "en" ? "selected" : "unselected"}
                onClick={this.handleClick.bind(this, "en:CA", "en")}
              >
                En
              </a>
              <ReactTooltip place="top" type="dark" effect="float" id="english">
                English
              </ReactTooltip>
            </li>
            <li>
              <a
                data-tip
                data-for="spanish"
                className={shortLanguage == "es" ? "selected" : "unselected"}
                onClick={this.handleClick.bind(this, "es:MX", "es")}
              >
                Es
              </a>
              <ReactTooltip place="top" type="dark" effect="float" id="spanish">
                EspaÃ±ol
              </ReactTooltip>
            </li>
            <li>
              <a
                data-tip
                data-for="french"
                className={shortLanguage == "fr" ? "selected" : "unselected"}
                onClick={this.handleClick.bind(this, "fr:CA", "fr")}
              >
                Fr
              </a>
              <ReactTooltip place="top" type="dark" effect="float" id="french">
                FranÃ§ais
              </ReactTooltip>
            </li>
            <li>
              <a
                data-tip
                data-for="italian"
                className={shortLanguage == "it" ? "selected" : "unselected"}
                onClick={this.handleClick.bind(this, "it:IT", "it")}
              >
                It
              </a>
              <ReactTooltip place="top" type="dark" effect="float" id="italian">
                Italiano
              </ReactTooltip>
            </li>
            <li>
              <a
                data-tip
                data-for="turkish"
                className={shortLanguage == "tr" ? "selected" : "unselected"}
                onClick={this.handleClick.bind(this, "tr:TR", "tr")}
              >
                Tr
              </a>
              <ReactTooltip place="top" type="dark" effect="float" id="turkish">
                TÃ¼rk
              </ReactTooltip>
            </li>
          </ul>
        </small>
      </Fragment>
    );
  }
}
