import React, { Fragment, useState, useEffect } from "react";
import Menu from "@material-ui/core/Menu";
import { Link } from "react-router-dom";

export default function SimpleMenu() {
	const [anchorEl, setAnchorEl] = React.useState(null);

	function handleClick(event) {
		setAnchorEl(event.currentTarget);
	}

	function handleClose() {
		setAnchorEl(null);
	}

	function handleTop() {
		window.scrollTo(0, 0);
	}

	return (
		<Fragment>
			<Link to="/login" onClick={handleTop} className="link-login">
				OASYS
			</Link>

			<a
				className="link-menu"
				aria-controls="simple-menu"
				aria-haspopup="true"
				onClick={handleClick}
			>
				<i className="fas fa-bars"></i>
			</a>
			<Menu
				id="simple-menu"
				anchorEl={anchorEl}
				keepMounted
				onClose={handleClose}
				open={Boolean(anchorEl)}
				onClick={handleClose}
			>
				<div className="layout-column" style={{ outline: "none" }}>
					<a onClick={handleClose} className="link-close--menu">
						<i className="material-icons">close</i>
					</a>
					<Link to="/dashboard" className="link-dialog">
						Activity Summary
					</Link>
					<Link to="/programs" className="link-dialog">
						Programs
					</Link>
					<Link to="/providers" className="link-dialog">
						Providers
					</Link>
					<Link to="/support" className="link-dialog">
						Support
					</Link>
					<a
						href="https://www.oneworldaccuracy.com/1wa/#/sustainability"
						className="link-dialog"
					>
						Sustainability
					</a>
					<a
						href="https://www.oneworldaccuracy.com/1wa/#/scientists"
						className="link-dialog"
					>
						Create EQA Programs | Scientists
					</a>
					<a
						href="https://www.oneworldaccuracy.com/1wa/#/laboratories"
						className="link-dialog"
					>
						Provide EQA Programs | Laboratories
					</a>
					<a
						href="https://www.oneworldaccuracy.com/1wa/#/governments"
						className="link-dialog"
					>
						Support National EQA Programs | Governments
					</a>
					<a
						href="https://www.oneworldaccuracy.com/1wa/#/commercial-distributors"
						className="link-dialog"
					>
						Develop EQA Market Leaders | Commerical Distributors
					</a>
					<a
						href="https://www.oneworldaccuracy.com/1wa/#/eqa-providers"
						className="link-dialog"
					>
						Deliver EQA Informatics | EQA Providers
					</a>
				</div>
			</Menu>
		</Fragment>
	);
}
