import React, {Fragment} from "react";
import UserSettings from './UserSettings';
import UserOasys from './UserOasys';
import { Link } from 'react-router-dom';

export default class Top extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      login: [],
      loading: true,
      isActive: false,
      showMenu: false
    }

  }

  componentDidMount() {
    window.addEventListener('scroll', this.handleScroll);
  }

  componentWillUnmount () {
    window.removeEventListener('scroll', this.handleScroll);
  }

  handleScroll = () => {
    if (window.scrollY < 60) {
        this.setState({ showMenu: false });
    } else {
        this.setState({ showMenu: true });
    }
}
  
render() {

  return (

    <Fragment>

        <div className="nav-activities">

          <div className={this.state.showMenu ? 'nav-activities-left nudge-right desktop' : 'nav-activities-left desktop'}>
          { this.state.showMenu && 
          <Link to="/" className="link-inert">
            <img src="/img/logo-mark-white.png" className="fade-in" height="25" />
          </Link> } Our Mission.  To achieve universal testing accuracy for improved healthcare for all people. 
          </div>

          <div className={this.state.showMenu ? 'nav-activities-right nudge-left' : 'nav-activities-right nudge-right'}>
            <UserSettings /> 
            {this.state.showMenu && <UserOasys />}
          </div>

        </div>
            
      </Fragment>

    )
  }
};